                <p>{{ currentHotel.phone }}</p>     
      </div>
    </div>

      <div class="container" >
        <div class="row d-flex justify-content-center flex-nowrap">

          <div 
            v-for="room in rooms"
            :key="room.id"
            class="card" style="width: 18rem;"
          >
            <img class="card-img-top" src="https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Ftse1.mm.bing.net%2Fth%3Fid%3DOIP.xv2M6xuhYjmb4-MckwBuywHaFS%26pid%3DApi&f=1" alt="Head img">
            <div class="card-body">
              <h5 class="card-title">Habitaci贸n: {{ room.room }}</h5>
              <ul class="list-group list-group-flush">
                <li class="list-group-item">Descripci贸n: {{ room.description }}</li>
                <li class="list-group-item">Tipo: {{ room.type }}</li>
                <li class="list-group-item">Capacidad: {{ room.capacity }}</li>
                <li class="list-group-item">Precio: {{ room.price }}</li>
                <li class="list-group-item">Disponible: {{ room.is_available }}</li>

                <li class="list-group-item">
                  <a class="btn btn-primary">Reservar</a>
                </li>
              </ul>
            </div>
          </div>
        
        </div>
      </div>

    <p>{{ message }}</p>

  </div>

  
  
</template>

<script>
import ReservasDataService from "../services/ReservasDataService";

export default {
  name: "hotel",
  data() {
    return {
      currentHotel: null,
      rooms: null,
      message: ''
    };
  },
  methods: {
    getHotel(id) {
      ReservasDataService.getHotel(id)
        .then(response => {
          this.currentHotel = response.data;
          //this.message = response.data;
          this.getAllRoomsFromHotel(this.currentHotel.id);
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },
    getAllRoomsFromHotel(id){
        ReservasDataService.getAllRoomsByHotel(id)
        .then(response => {
            this.rooms = response.data;
            //this.message = response.data;
        })
        .catch ( e => {
            this.message = e;
        });

    }

  },
  mounted() {
    this.message = '';
    this.getHotel(this.$route.params.id);
  }
};
</script>

